    text-align: center;
    line-height: 45px;
    color: #ffffff;
    font-size: 28px;
    @include border-radius(5px);
    @include gradient-two;
    cursor: pointer;
    z-index: 1;
    &.prev {
      left: -20px;
      @media (max-width: 575px) {
        left: 0; } }
    &.next {
      right: -20px;
      @media (max-width: 575px) {
        right: 0; } } }
  .slick-list {
    margin: -15px; }
  .post-card {
    margin: 15px; } }
/* blog section css end */

/* blog details section css start */
.blog-details {
  margin-top: -165px;
  &__header {
    padding: 80px 95px;
    background-color: rgb(22, 3, 81);
    @include border-radius(20px);
    @media (max-width: 991px) {
      padding: 50px 30px; } }
  &__title {
    font-size: 60px;
    text-align: center;
