  .social-links {
    align-items: center;
    justify-content: flex-end;
    @media (max-width: 991px) {
      justify-content: center;
      margin-top: 15px; }
    li {
      span {
        color: #ffffff; }
      a {
        width: 44px;
        height: 44px;
        line-height: 46px;
        background-color: rgba(104, 61, 245, 0);
        border: 1px solid rgba(#ffffff, 0.2);
        &:hover {
          background-image: -moz-linear-gradient( 86deg, rgb(236,3,139) 0%, rgb(251,100,104) 44%, rgb(251,185,54) 100%);
          background-image: -webkit-linear-gradient( 86deg, rgb(236,3,139) 0%, rgb(251,100,104) 44%, rgb(251,185,54) 100%);
          background-image: -ms-linear-gradient( 86deg, rgb(236,3,139) 0%, rgb(251,100,104) 44%, rgb(251,185,54) 100%);
          box-shadow: 0px 17px 40px 0px rgba(124, 78, 25, 0.35); } } } }
  &__content {
    p {
      margin-top: 20px;
      a {
        color: #6157f9;
        text-decoration: underline; } }
    blockquote {
      margin-top: 40px;
      padding: 30px 45px;
      background-color: #05023b;
      @include border-radius(20px);
      @media (max-width: 575px) {
        padding: 20px; }
      p {
        margin-top: 0;
        font-size: 28px;
        color: #d2ddfb;
        font-weight: 400;
        @media (max-width: 991px) {
          font-size: 20px; }
        @media (max-width: 575px) {
          font-size: 16px; } } }
    .img-holder {
      img {
        @include border-radius(20px); } }
    .video-thumb {
      position: relative;
      overflow: hidden;
      @include border-radius(10px);
      &__icon {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 120px;
        height: 120px;
        margin-top: -60px;
        margin-left: -60px;
        background-color: rgba(114, 56, 196, 0.8);
        color: #ffffff;
        font-size: 52px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0px 27px 103px 0px rgba(7, 11, 78, 0.67);
        @include border-radius(50%);
        animation: pulse 1.5s infinite;
