          height: 100px;
          margin-top: -50px;
          margin-left: -50px;
          font-size: 36px; } } } }
  &__sub-title {
    font-size: 36px;
    margin-bottom: 20px;
    @media (max-width: 991px) {
      font-size: 28px; }
    @media (max-width: 767px) {
